@media (max-width: 500px) {
  #home .container {
    height: 50vh;
  }
  #nav-bar {
    display: block;
  }
  #nav-bar .logo h2 {
    display: flex;
    font-size: 1.7rem;
    justify-content: center;
  }
  #nav-bar ul {
    padding-top: 1.5rem;
    justify-content: center;
    padding-bottom: 3rem;
  }
  .more {
    top: -130px;
  }
  /*Section2 WORK  */
  #work .skills {
    flex-direction: column;
    width: 100% !important;
  }
  #project {
    width: 100%;
  }
  .p-2 {
    padding: 1rem 0.3rem;
  }
  .popular-projects {
    overflow-x: scroll;
  }
  .table tr th {
    font-size: 0.9rem;
  }
  .table tr td {
    font-size: 1.2rem;
  }

  #footer .social-links .social-icons i {
    margin: 0.2rem;
  }
  /* contact */
  #contact {
    min-height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .form {
    height: 100%;
    margin-top: -4rem;
  }

  .form h2 {
    padding: 0rem !important;
  }
  .form input {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  #contact .form h2 {
    width: auto;
  }
  .input,
  select {
    width: 90%;
  }
  #contact {
    height: 100%;
  }
  .submit {
    padding: 2.3rem;
  }
}

/* iphone 5 */
@media (max-height: 568px) {
  .more {
    top: -5px;
  }
  #home .container h1 {
    letter-spacing: 10px;
  }
  #work .skills {
    flex-direction: column;
    width: 100% !important;
  }

  .work-info h3 {
    font-size: 1.5rem;
  }
  #footer .social-links .social-icons i {
    margin: 0.1rem;
  }
}

@media (max-width: 1420px) {
  #work .skills {
    flex-direction: column;
    width: 80%;
  }
}
