@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Roboto", sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}
/* NAVBAR SECTION */
#nav-bar {
  display: flex;
  padding: 2rem 0;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.logo h2 {
  padding: 0rem 1rem;
  color: white;
  cursor: pointer;
}
.text-primary a {
  color: white;
}
#nav-bar {
  display: flex;
  color: white;
  justify-content: space-between;
  z-index: 1;
  padding: 1rem;
}
#nav-bar ul {
  display: flex;
  align-items: center;
  list-style: none;
}
#nav-bar ul li {
  color: #fff;
  padding: 0.75rem 0.75rem;
  margin: 1rem;
  transition: all 0.3s ease-in-out;
}

#nav-bar ul li:hover {
  background-color: rgba(67, 73, 72, 0.2);
}
/* Homescreen */
#home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  width: 100%;
  overflow-y: hidden;
  overflow-x: hidden;
  background: linear-gradient(rgba(0, 0, 0, 0.9), transparent),
    url(/Tonyibra.github.io/static/media/mainbackground.f7daab03.jpg) no-repeat center center/cover;
}
#home .container {
  height: 100%;
  width: 75%;
  margin: auto;

  justify-content: center;

  color: white;
  flex-direction: column;
}
#home .container h1 {
  letter-spacing: 20px;
  font-size: 3rem;
  line-height: 1.1;
  font-weight: 500;
}
#home .container h3 {
  font-size: 1.2rem;
  font-weight: 400;
  width: 100%;
}
.mg-1 i {
  margin: 1rem 0;
}
.social-link i {
  display: flex;
  width: 25px;
  height: 25px;
  align-items: center;
  justify-content: center;
  border-radius: 70%;
  cursor: pointer;
  background: white;
}
.fb-color i {
  background-color: #236ee5;
}
.tw-color i {
  background-color: #2ea1f2;
}
.social-link {
  position: absolute;
  top: 50%;
  padding: 0rem 0.3rem;
}
.more {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: white;
  font-size: 1rem;
  padding-bottom: 3rem;
  position: relative;
}
.more a {
  font-weight: 200 !important;
  padding: 1rem;
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
}
.more i {
  position: absolute;
  top: 50%;
  animation-name: arrow;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}
/* ahref for icons config */
a {
  text-decoration: none;
  list-style: none;
}

a i {
  color: black;
}
/* keyframe */

@keyframes arrow {
  from {
    top: 50%;
  }
  to {
    top: 55%;
  }
}
/* Section2 MY WORK */
#work {
  min-height: 100vh;
  width: 100%;
  background: #191919;
  color: white;
}
#work .container {
  width: 100%;
  height: 100%;
  padding-top: 5rem;
  display: flex;
}
#work .container .work-info {
  width: 70%;
  margin: auto;
  display: flex;
  flex-direction: column;
}
#work .container .work-info h3 {
  letter-spacing: 15px;
  padding-bottom: 60px;
  font-size: 1.7rem;
}
#work .container .work-info .work-desc {
  font-size: 1.2rem;
  padding-bottom: 3rem;
}
#work .skills {
  width: 65%;
  height: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.skills .skills-info {
  flex: 1 1;
  flex-wrap: wrap;
  padding: 1rem 1rem;
  border: 1px solid #333;
  border-radius: 20px;
  margin: 1rem;
}
.skills .skills-info h5 {
}
.skills h5 {
  font-size: 1.3rem;
  padding-bottom: 1rem;
}
.skills-info a {
  color: white;
}
.skills-info a:hover {
  border-bottom: 1px white solid;
  padding: 0rem;
  margin: 0rem;
}
/* Projects */
.p-2 {
  padding: 4rem 3rem;
}

#project {
  min-height: 70vh;
  width: 70%;
  margin: auto;
}
#project .popular-projects {
  /* table overflow */
  height: 100%;
  overflow-y: auto;
}
#project .popular-projects h2 {
  font-weight: 400 !important;
  margin-bottom: 0.5rem;
  padding-bottom: 1.2rem;
}
.table thead th,
.table tbody td {
  vertical-align: bottom;
  padding: 0.75rem;
  vertical-align: top;
  border-top: 1px solid #e9ecef;
  width: 7%;
}
.table tr th {
  font-weight: bold;
  text-align: left;
}
.table {
  border-collapse: collapse;
}
.popular-projects {
  padding-bottom: 5rem;
}
.future h2 {
  font-weight: 200;
  font-size: 1.3rem;
}
.future .future-list {
  padding: 0.75rem 2rem;
}
.future h3 {
  padding-top: 2rem;
  font-weight: 300;
  font-size: 1.2rem;
}
.contact-btn {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 3rem;
}
.contact-btn button {
  padding: 0.5rem 0.5rem;
  color: #fff;
  background-color: #343a40;
  border-color: #343a40;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
/* Footer */
#footer .social-links {
  background: #282828;
  height: 30vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: white;
}
#footer .social-links h5 {
  font-weight: 200 !important;
}
#footer .social-links .social-icons {
  display: flex;
  flex-direction: row;
  margin: 2rem;
}
#footer .social-links i {
  margin: 1rem;
  display: flex;
  width: 50px;
  height: 50px;
  align-items: center;
  justify-content: center;
  border-radius: 70%;
  cursor: pointer;
  background: white;
}
.page-ending {
  padding: 0rem 2rem;
  display: flex;
  height: 10vh;
  background: #191919;
  width: 100%;
  color: white;
  justify-content: space-around;
  align-items: center;
}
.page-ending li {
  list-style: none;
  cursor: pointer;
}
.page-ending p {
  color: #323232;
}
/* contact me */
#contact {
  background-color: #333;
  color: black;
  height: 100vh;
}
#contact .form {
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#contact .form h2 {
  width: 55vh;
  color: white;
  font-weight: 200 !important;
  font-size: 1.3rem;
  padding-bottom: 2rem;
}

#contact .form label {
  color: white;
}
.input,
select {
  width: 55vh;
}
.input label,
.input select {
  display: block;
  margin: 0.75rem 0;
}
.input input,
.input select,
.input textarea {
  display: block;
  padding: 0.5rem;
  width: 100%;
  color: #fff;
  border-radius: 0.25rem;
  border: 1px solid rgba(255, 255, 255, 0.15);
  background-clip: padding-box;
  background-color: #333;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}
.input input:focus {
  background-color: #333 !important;
  border-color: #b0bdff !important;
  outline: 0;
}
.input select:focus {
  background-color: #333 !important;
  border-color: #b0bdff !important;
  outline: 0;
}
.input textarea:focus {
  background-color: #333 !important;
  border-color: #b0bdff !important;
  outline: 0;
}
textarea {
  resize: none;
}
.submit {
  display: flex;
  justify-content: center;
  align-items: center;
}
.submit button {
  display: block;
  padding: 0.5rem 1.5rem;
  color: #343a40;
  background-color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 5px;

  cursor: pointer;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}
.submit button:focus {
  background-color: #fff !important;
  border-color: #b0bdff !important;
  outline: 0;
}

@media (max-width: 500px) {
  #home .container {
    height: 50vh;
  }
  #nav-bar {
    display: block;
  }
  #nav-bar .logo h2 {
    display: flex;
    font-size: 1.7rem;
    justify-content: center;
  }
  #nav-bar ul {
    padding-top: 1.5rem;
    justify-content: center;
    padding-bottom: 3rem;
  }
  .more {
    top: -130px;
  }
  /*Section2 WORK  */
  #work .skills {
    flex-direction: column;
    width: 100% !important;
  }
  #project {
    width: 100%;
  }
  .p-2 {
    padding: 1rem 0.3rem;
  }
  .popular-projects {
    overflow-x: scroll;
  }
  .table tr th {
    font-size: 0.9rem;
  }
  .table tr td {
    font-size: 1.2rem;
  }

  #footer .social-links .social-icons i {
    margin: 0.2rem;
  }
  /* contact */
  #contact {
    min-height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .form {
    height: 100%;
    margin-top: -4rem;
  }

  .form h2 {
    padding: 0rem !important;
  }
  .form input {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  #contact .form h2 {
    width: auto;
  }
  .input,
  select {
    width: 90%;
  }
  #contact {
    height: 100%;
  }
  .submit {
    padding: 2.3rem;
  }
}

/* iphone 5 */
@media (max-height: 568px) {
  .more {
    top: -5px;
  }
  #home .container h1 {
    letter-spacing: 10px;
  }
  #work .skills {
    flex-direction: column;
    width: 100% !important;
  }

  .work-info h3 {
    font-size: 1.5rem;
  }
  #footer .social-links .social-icons i {
    margin: 0.1rem;
  }
}

@media (max-width: 1420px) {
  #work .skills {
    flex-direction: column;
    width: 80%;
  }
}

